* {
  font: inherit;
  vertical-align: baseline;
  margin: 0;
  padding: 0;
  font-size: 100%;
}

*, :before, :after {
  box-sizing: border-box;
}

ol, ul {
  list-style: none;
}

html, body {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  text-rendering: optimizespeed;
  -webkit-font-smoothing: antialiased;
  line-height: 1.5;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *, :before, :after {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

a:visited, a:link, a:active {
  color: currentColor;
  text-decoration: none;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

article, aside, details, figcaption, footer, header, hgroup, menu, nav, section {
  display: block;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  font-family: Roboto, sans-serif;
}

body {
  height: 100vh;
  background-color: #8bc6ec;
  background-image: linear-gradient(135deg, #8bc6ec 0%, #9599e2 100%);
  overflow: hidden;
}

.login-container {
  width: 400px;
  background: #fff;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 10px 10px 15px #0000000d;
}

.login-container h1 {
  text-align: center;
  letter-spacing: .15ch;
  border-bottom: 1px solid silver;
  padding: 30px 0;
  font-size: x-large;
  font-weight: 600;
}

.login-container p {
  text-align: center;
  color: #639;
}

.login-container form {
  box-sizing: border-box;
  padding: 0 40px 50px;
}

form .form_input {
  border-bottom: 2px solid #adadad;
  margin: 50px 0;
  position: relative;
}

.form_input:has(input[type="text"]) {
  margin-bottom: 80px;
}

.form_input:has(input[type="password"]) {
  margin-bottom: 70px;
}

.form_input input {
  width: 100%;
  height: 40px;
  background: none;
  border: none;
  outline: none;
  padding: 0 5px;
  font-size: 16px;
}

.form_input label {
  color: #adadad;
  pointer-events: none;
  font-size: 16px;
  transition: all .5s;
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
}

.form_input span:before {
  content: "";
  width: 0%;
  height: 2px;
  background: #2d343c;
  transition: all .5s;
  position: absolute;
  top: 40px;
  left: 0;
}

.form_input input:focus ~ label, .form_input input:valid ~ label {
  color: #2d343c;
  top: -5px;
}

.form_input input:focus ~ span:before, .form_input input:valid ~ span:before {
  width: 100%;
}

input[type="submit"] {
  width: 100%;
  height: 50px;
  color: #e9f4fb;
  cursor: pointer;
  background: #2d343c;
  border: 1px solid;
  border-radius: 25px;
  outline: none;
  font-size: 18px;
  font-weight: 700;
}

input[type="submit"]:hover {
  border-color: #2d343c;
  transition: all .5s;
}

@media only screen and (max-width: 500px) {
  .login-container {
    max-width: 300px;
  }

  .login-container form {
    padding: 0 25px 50px;
  }
}

